.roundy {
  font-size: min(4vh, 5vw);
  width: 20em;
  height: 20em;
  transform-origin: center;
  position: absolute;
  font-weight: 900;
  color: #fff;
  filter: blur(3px);
  /* right: -10em; */
  top: 0;
  animation: letterTr 60s linear infinite;
}

.letter {
  position: absolute;
  transform-origin: center;
  font-size: 20em;
  line-height: 1;
  width: 1em;
  height: 1em;
  text-align: center;
  /* -webkit-text-stroke-width: 30px;
    color: #fafafa00;
    -webkit-text-stroke-color: #fff; */
}

@keyframes letterTr {
  to {
    transform: rotateZ(360deg);
  }
}