.works {
  min-height: min(130vw, 100vh);
  width: min(100vw, 100vh);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform-style: preserve-3d;

  .text {
    width: min(90vw, 100vh);
    font-size: min(9vh, 9vw);
    position: relative;
    transform: translateZ(250px) scale(0.7);
    line-height: 1.4;
    font-family: "Harmond-ItalicCondensed";
    transform-style: preserve-3d;

    .work {
      position: relative;
      transform-style: preserve-3d;

      h1 {
        /* text-decoration: underline; */
        font-size: 1em;
        font-weight: 100;
        border-bottom: 0.05em solid;
        z-index: 1;
        position: relative;
        transition: color 0.3s;
      }
    }

    .work:hover {
        z-index: 10;
      h1 {
        mix-blend-mode: difference;
        color: #fafafa;
        z-index: 10;
      }

      .worksImage {
        opacity: 1;
        z-index: 9;
        transform: translateX(20%) translateY(-50%) rotate(5deg);
      }
    }
  }
}

.worksImage {
  position: absolute;
  height: 50vh;
  top: 60%;
  right: 0;
  transform: translate(20%, -50%);
  z-index: 0;
  opacity: 0;
  transition: opacity 0.5s, transform 0.3s;
  /* animation: float 3s ease-in-out infinite; */
  pointer-events: none;
}

@keyframes float {
    0%,
    100% {
      transform: translateX(20%) translateY(-50%) rotate(5deg);
    }
  
    50% {
      transform: translateX(20%) translateY(-52%) rotate(5deg);
    }
  }

.roundy {
  font-size: min(4vh, 5vw);
  width: 20em;
  height: 20em;
  transform-origin: center;
  position: absolute;
  font-weight: 600;
  color: #fff;
  filter: blur(3px);
  left: -10em;
  top: 0;
}
