.backgroundGradient {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  mask-image: url("./grain.png"), linear-gradient(45deg, #fff 0%, transparent 25%, transparent 75%, #fff 100%);
  background: radial-gradient(circle at 0% 100%, #6d6dff88 0%, transparent 30%), radial-gradient(circle at 100% 0%, #fbffbe99 0%, transparent 35%);

  .bgGrSphere {
    width: min(70vh, 70vw);
    height: min(70vh, 70vw);
    position: absolute;
    z-index: -2;
    top: 25%;
    left: 25%;
    background: radial-gradient(circle at 50%, #6d6dff88 0%, transparent 70%);
    animation: bgFloat 20s infinite;
  }
}

.backgroundBlur {
  display: flex;
  position: fixed;
  width: 120vw;
  flex-wrap: wrap;
  height: 120vh;
  filter: blur(15vh);
  top: -10vh;
  left: -10vw;
}

.square {
  width: 33.33333333%;
  height: 33.3333333%;
  animation: colorchange 9s ease-in infinite;
}

.square:nth-child(2) {
  animation-delay: -1s;
}
.square:nth-child(3) {
  animation-delay: -2s;
}
.square:nth-child(4) {
  animation-delay: -6.5s;
}
.square:nth-child(5) {
  animation-delay: -4.5s;
}
.square:nth-child(6) {
  animation-delay: -5.5s;
}
.square:nth-child(7) {
  animation-delay: -5s;
}
.square:nth-child(8) {
  animation-delay: -7.5s;
}
.square:nth-child(9) {
  animation-delay: -3.5s;
}

@keyframes colorchange {
  0%,
  100% {
    background: #fcccff;
  }
  33% {
    background: #6d6dff;
  }
  66% {
    background: #bddbff;
  }
}


.grainOverlay::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(252, 204, 255, 0.3);
  mask-size: 10vh;
  mask-image: url("grain.png");
}