.container {
  top: 50%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  width: min(100vw, 90vh) !important;
  height: min(100vw, 90vh) !important;
 

  canvas {
    position: absolute;
    width: 100% !important;
    height: 100% !important;
    -webkit-mask-image: -webkit-gradient(linear, left 75%, left 90%, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0)));
  }
}
