.contact {
  min-height: min(100vw, 100vh);
  width: min(100vw, 100vh);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* transform-style: preserve-3d; */

  .text {
    /* width: min(65vw, 100vh); */
    width: 100%;
    height: 70%;
    font-size: min(3vh, 5vw);
    position: absolute;
    /* transform: translateZ(200px) scale(0.8); */
    line-height: 1.4;

    div {
      position: absolute;
      width: 100%;
      height: 100%;
    }

    svg {
      position: absolute;
      font-family: "Harmond-ItalicCondensed";
      font-size: 15vh;
      line-height: 1;

      &.isMobile {
        font-size: 25vh;
      }
    }
  }
}

.roundy {
  font-size: min(4vh, 5vw);
  width: 20em;
  height: 20em;
  transform-origin: center;
  position: absolute;
  font-weight: 900;
  color: #fff;
  filter: blur(3px);
  /* right: -10em; */
  /* transform: translateZ(250px) scale(0.7); */
}

path {
  fill: transparent;
  /* stroke: black; */
}

