.about {
  min-height: min(130vw, 100vh);
  width: min(100vw, 100vh);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform-style: preserve-3d;

  .text {
    width: min(65vw, 100vh);
    font-size: min(3vh, 5vw);
    position: relative;
    /* transform: translateZ(250px) scale(0.7); */
    line-height: 1.4;

    b {
      font-weight: 600;
      text-decoration: underline;
    }

    .wave {
        width: 1.5em;
        height: 1.5em;
        animation-name: wave-animation;
        animation-duration: 3s;
        /* animation-delay: 2s; */
        animation-iteration-count: 2;
        transform-origin: 70% 70%;
        display: inline-block;
        margin-bottom: -0.25em;
        padding-right: 0.25em;
      }
  }

  .text:hover .wave {
    animation-iteration-count: infinite;
  }
}

.roundy {
  font-size: min(4vh, 5vw);
  width: 20em;
  height: 20em;
  transform-origin: center;
  position: absolute;
  font-weight: 900;
  color: #fff;
  filter: blur(3px);
  right: -10em;
  /* transform: translateZ(250px) scale(0.7); */
}

@media (orientation: landscape) {
  .text {
    background: rgb(255, 255, 255);
    background: linear-gradient(145deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.1) 100%);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 2px outset rgba(255, 255, 255, 0.3);
    box-shadow: 0px 1px 24px -1px rgba(0, 0, 0, 0.2);
    border-radius: 0.75em;
    padding: 1em 2em;
  }

  .text::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    border-radius: 1em;
    opacity: 0.02;
    pointer-events: none;
    background-image: url("grain.png");
  }
}

@keyframes wave-animation {
    0% {
      transform: rotate(0deg);
    }
  
    5% {
      transform: rotate(-5deg);
    }
  
    10% {
      transform: rotate(10deg);
    }
  
    20% {
      transform: rotate(-10deg);
    }
  
    30% {
      transform: rotate(12deg);
    }
  
    40% {
      transform: rotate(-10deg);
    }
  
    50% {
      transform: rotate(12deg);
    }
  
    60% {
      transform: rotate(-10deg);
    }
  
    70% {
      transform: rotate(12deg);
    }
  
    80% {
      transform: rotate(-10deg);
    }
  
    90% {
      transform: rotate(9deg);
    }
  
    100% {
      transform: rotate(0deg);
    }
  }
  