.menu {
  background: #000c;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: -100%;
  opacity: 0;
  z-index: 1;
  transition: opacity 0.2s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'syne', serif;
  backdrop-filter: blur(20px);

  &.opened {
    opacity: 1;
    right: 0;
  }

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    pointer-events: none;
    background: url("./plastic.png");
    background-size: cover;
    opacity: 0.5;
  }

  a {
    color: inherit;
    text-decoration: inherit;
    position: relative;
  }
}

.menuItem {
  font-weight: 900;
  font-size: min(10vh, 10vw);
  position: relative;
  height: 1.5em;
  color: #2e2e2e00;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fafafa;

  &:before {
    top: 0;
    left: 0;
    width: 0;
    color: #fafafa;
    overflow: hidden;
    position: absolute;
    content: attr(data-text);
    transition: all 0.5s;
  }

  &:hover:before {
    width: 100%;
  }
}

.menuButton {
  z-index: 2;
  position: fixed;
  right: 16px;
  top: 16px;
  width: 32px;
  height: 32px;
  opacity: 0.8;

  &:hover {
    opacity: 1;
  }

  &:before,
  &:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 4px;
    background-color: #2e2e2e;
    transition: transform 0.5s, background-color 0.5s;
  }

  &.close:before,
  &.close:after {
    background-color: #fff;
  }
}

.menuButton:before {
  transform: rotate(90deg) translateX(5px);
}

.menuButton:after {
  transform: rotate(-90deg) translateX(5px);
}

.menuButton.close:before {
  transform: rotate(45deg);
}

.menuButton.close:after {
  transform: rotate(-45deg);
}