.scroll {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(-100%);
  text-align: center;
  bottom: 1em;
  opacity: 1;
  transition: opacity 0.3s;

  &:after {
    content: "";
    position: absolute;
    width: 1px;
    height: 5em;
    background: linear-gradient(#444, #4440);
    left: 50%;
    top: 100%;
  }

  .hide {
    opacity: 0;
  }
}